*{
  color: var(--main-font-color);
  font-size:11pt;
  font-family: 'Montserrat', sans-serif;
}
.brand{
  padding:0 15px;
  margin:0;
  height:100%;
  display:flex;
  justify-content: space-between;
  align-items: center;
  span{
    color: var(--main-primary-color);
    text-decoration: none !important;
  }
}
.box-header-brand{
  height: 54px;
  border-bottom: 0.0625rem solid rgb(233, 236, 239);
}
.link-brand{
  margin-top: 10; 
  padding: 4;
  text-decoration: none;
}
.burger-chat{
  position: fixed;
  right: 16px;
}
.class-list-item{
  margin-top:1px;
  border-bottom: 0.0625rem solid rgb(244, 244, 244);
  border-top: 0.0625rem solid #fff;
  padding:5px 0;
  div{
    font-size:10pt;
  }
}
.active{
  background-color: #f1f3f5;
}
.class-list-item-button{
  width: 100%;
  padding: 0.625rem;
  border-radius: 0.25rem;
}

.text-search{
  box-sizing: border-box;
  padding: 0.25rem;
  background: #fff;
  border-bottom: 0.0625rem solid #e9ecef;
}
.separator{
  border-top: 1px solid #e9ecef;
  border-bottom: 1px solid #fff;
  margin-bottom:10px;
}
.bottom-infos-plan-user{
  padding-bottom: 10px;
  .class-list-item-button{
    margin:0 20px;
    padding:0
  }
}
.button-chat-free{
  span{
    color:#FFF !important;
    font-size:10pt
  }
}

.text-description{
  p{
    font-size:10pt !important;
    text-align: center;
    font-weight: 300;
    padding:0 2em;
  } 
  svg{
    animation: arrow-animation 2s infinite;
  }
}
@keyframes arrow-animation {
  0% {transform: translateY(0px);}
  50% {transform: translateY(5px);}
  100% {transform: translateY(0px);}
}

.chat-header{
  text-align: center;
  line-height: 25pt;
  display: flex;
  justify-content: center;
  button{
    margin-left:20px;
  }
  
}