.header-chat{
  border-bottom:#E9ECEF solid 1px;
  height:59px;
  display: flex;
  justify-content: center;
  align-items: center;
  &--button{
    border-radius: 5px;
    border:0;
    background-color: #009B3A;
    color:#FFF;
    padding:.5em 1em;
    margin-left:10px;
  }
}
.index-chat{ 
  background-position: center 20px;
  background-repeat:  no-repeat;
  height: 100%;
  max-width: 1000px;
  max-height: 700px;
  margin:auto;
  margin-top:10em;

  &--title{
    font-size: 26px;
    font-weight: 700;
    line-height: 18px;
    letter-spacing: 0em;
    text-align: center;
    margin-top:2.8em;
  }

  &--description{
    margin-top:3em;
    padding:0 3em;
    text-align: center;
    font-size: 16px;
    font-weight: 400;
    line-height: 23px;
    letter-spacing: 0em;
    text-align: center;
  }

  &--sub-description{
    font-size: 18px;
    font-weight: 500;
    line-height: 23px;
    letter-spacing: 0em;
    text-align: center;
    margin-top:5em;
    padding:0 3em;
    line-height: 23px;
    letter-spacing: 0em;
    text-align: center;
  }

  &__benefits{
    margin:3em 3em;
    display: flex;
    justify-content: space-around;
    
    &__item{
      border-radius: 8px;
      width: 287px;
      height: 244px;
      text-align: center;
      border:#E9ECEF solid 1px;

      &--icon{
        margin-top:2em;
      }
      &--title{
        font-size: 14px;
        font-weight: 600;
        line-height: 23px;
        letter-spacing: 0em;
        text-align: center;
      }
      &--description{
        padding:0 2em;
        font-size: 9pt;
        font-weight: 400;
        line-height: 23px;
        letter-spacing: 0em;
        text-align: center;

      }
    }
  }
}