.chat{
  display: flex;
  height:100%;
  height: 100vh;
  &__nav{
    max-height: 1200px;
    background-color: #F8F8FB;
    min-width: 300px;
    width: 300px;
    display: flex;
    flex-direction: column;
    a{
      text-decoration: none;
    }
    &__brand{
      border-bottom:#E9ECEF solid 1px !important;
      height:59px;
    }
    &__new-chat{
      height:55px;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      button{
        width: 94%;
        background-color: var(--main-second-color);
        height:40px;
        color: var(--main-font-color-white);
        font-size:1pt !important;
        color:#FFF;
        border-radius:4px;
        border:0;
        span{
          font-size:10pt
        }
      }
    }
    &__items{
      height:50px;
      margin-top:.3em;
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      button{
        width: 45%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        height: 36px;
        border-radius: 5px;
        background-color: transparent;
        font-weight: 600;
        span{
          color:#000
        }
      }
      &--active{
        box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 2px 0px;
        background-color: #FFF !important;
      }
    }

    &__search{
      margin-top:5px;
      display: flex;
      justify-content: center;
      &__base{
        box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 2px 0px;
        border-radius: 8px;
        height:45px;
        width: 89%;
        display: flex;
        justify-content: space-between;
        display: flex;
        align-items: center;
        margin-top:.2em;
        background-color: #FFF;
        padding:0em .5em;
        &--input{
          border:0;
          flex: .95;
          height:40px;
        }
      }
    }
  
    &__description{
      display: flex;
      flex-direction: column;
      padding: 0 1.5em;
      &--text{
        text-align: center;
        font-size:10pt;
      }
      &--icon{
        margin: -10px auto;
        animation: arrow-animation 2s infinite;
      }
    }

    &__chats{
      list-style: none;
      padding: 0;
      margin-top:20px;
      flex: 1;
      overflow: auto;
      &--item{
        margin:.4em 0em;
        &--active{
          button{
            background-color: #fff;
          }
        }
        &--button{
          width: 91%;
          display: flex;
          margin:0 1em;
          height:40px;
          background-color: transparent;
          padding: 0;
          &:hover{
            background-color: #FFF;
          }
          &--icon{
            width: 40px;
            margin-left:10px;
          }
          &--text{
            width: 100%;
            margin-left:10px;
            font-size: 10pt;
            color:var(--main-font-color-black);
            font-weight: lighter;
          }
        }
      }
    }
    &__bottom{
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      margin-bottom: 1em;
      &--item{
        width: 95%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin:.3em 0em;
        border:0;
        background-color: transparent;
        background-color: #FFF;
        border-radius: 5px;
        height:40px;
        cursor: pointer;
        &:hover{
          background-color: #ccc;
        }
        &--image{
          width: 18px;
        }
        &--text{
          flex: 1;
          text-align: left; 
          margin-left:5px;
          font-weight: 600;
        }
        &--button{
          color:#FFF;
          padding:.3em .5em;
          border-radius: 5px;
          background-color: #009B3A;
          font-size:10pt;
          font-weight: 600;
        }
      } 
    }
    &__account{
      &--hide{
        max-height: 0;
        transition: max-height 0.25s ease-in, opacity 1s ease-in;
        overflow: hidden;
        opacity: 0;
      }
      transition: max-height 0.25s ease-in, opacity 1s ease-in;
      max-height: 300px;
      overflow:auto;
      opacity: 1;

      display: flex;
      bottom:60px;
      width: 100%;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      &--item{
        width: 95%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border:0;
        background-color: transparent;
        background-color: #FFF;
        height:40px;
        cursor: pointer;
        &:hover{
          background-color: #ccc;
        }
        &--image{
          width: 18px;
        }
        &--text{
          flex: 1;
          text-align: left; 
          margin-left:5px;
          font-weight: 600;
        }
        &--button{
          color:#FFF;
          padding:.3em .5em;
          border-radius: 5px;
          background-color: #009B3A;
          font-size:10pt;
          font-weight: 600;
        }
      } 
    }

    
  }

  &__main{
    flex: 1;
    
  }
}

@keyframes fade-show {
  0% {opacity: 1;}
  100% {opacity: 0;}
}

@keyframes arrow-animation {
  0% {transform: translateY(0px);}
  50% {transform: translateY(5px);}
  100% {transform: translateY(0px);}
}